import { supabase } from "@/lib/supabase";
import Link from "next/link";
import { ArrowLeft, ShoppingBag } from "lucide-react";
import FullMarketplaceClient from "@/components/marketplace/FullMarketplaceClient";

// Force Dynamic rendering so we always get new products
export const dynamic = 'force-dynamic';

export default async function MarketplacePage() {
  
  // Fetch INITIAL batch (first 20)
  const { data: initialProducts } = await supabase
    .from("products")
    .select("*, stores(name, slug)") // Join store info
    .eq("is_active", true)
    .range(0, 19)
    .order("created_at", { ascending: false });

  const { data: initialStores } = await supabase
    .from("stores")
    .select("*")
    .limit(20);

  return (
    <div className="min-h-screen bg-gray-50 font-sans">
       {/* Simple Header */}
       <nav className="bg-white border-b border-gray-200 sticky top-0 z-40">
         <div className="max-w-7xl mx-auto px-4 h-16 flex items-center justify-between">
           <Link href="/" className="flex items-center gap-2 font-bold text-gray-600 hover:text-gray-900 transition">
             <ArrowLeft size={18} /> Back to Home
           </Link>
           <div className="font-extrabold text-xl tracking-tight text-gray-900 flex items-center gap-2">
             <ShoppingBag size={20}/> Marketplace
           </div>
           <div className="w-20"></div> {/* Spacer for centering */}
         </div>
       </nav>

       {/* Client Component handles Load More & Modals */}
       <FullMarketplaceClient 
         initialProducts={initialProducts || []} 
         initialStores={initialStores || []} 
       />
    </div>
  );
}